import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import M from 'materialize-css/dist/js/materialize.min.js';
import { updateLog } from '../../actions/logActions';
import TechSelectOptions from '../techs/TechSelectOptions';

const EditLogModal = ({ current, updateLog }) => {
  const [message, setMessage] = useState('');
  const [attention, setAttention] = useState(false);
  const [tech, setTech] = useState('');

  useEffect(() => {
    if (current) {
      setMessage(current.message);
      setAttention(current.attention);
      setTech(current.tech);
    }
  }, [current]);

  const onSubmit = () => {
    if (message === '' && tech === '') {
      M.toast({
        html: 'Please enter a message and tech.',
        classes: 'red accent-4',
      });
    } else if (message === '') {
      M.toast({
        html: 'Please enter a message.',
        classes: 'red accent-4',
      });
    } else if (tech === '') {
      M.toast({
        html: 'Please assign a tech.',
        classes: 'red accent-4',
      });
    } else {
      updateLog({ id: current.id, message, attention, tech, date: new Date() });
      M.toast({
        html: `Log updated by ${tech}.`,
      });
      // Clear fields
      setMessage('');
      setAttention(false);
      setTech('');
    }
  };

  return (
    <div id="edit-log-modal" className="modal" style={modalStyle}>
      <div className="modal-content">
        <h4 className="green-text text-darken-4">Edit System Log</h4>
        <div className="row">
          <div className="input-field">
            <input
              type="text"
              name="message"
              value={message}
              onChange={e => setMessage(e.target.value)}
            />
          </div>
        </div>

        <div className="row">
          <div className="input-field">
            <select
              name="tech"
              value={tech}
              className="browser-default"
              onChange={e => setTech(e.target.value)}
            >
              <option value="" disabled>
                Select Technician
              </option>
              <TechSelectOptions />
            </select>
          </div>
        </div>

        <div className="row">
          <div className="input-field">
            <p>
              <label>
                <input
                  type="checkbox"
                  checked={attention}
                  value={attention}
                  onChange={e => setAttention(!attention)}
                />
                <span>Needs Attention</span>
              </label>
            </p>
          </div>
        </div>
      </div>
      <div className="modal-footer">
        <a
          href="#!"
          onClick={onSubmit}
          className={`${
            message !== '' &&
            tech !== '' &&
            'modal-close waves-effect waves-light'
          } btn green darken-4`}
        >
          Enter
        </a>
      </div>
    </div>
  );
};

const modalStyle = {
  width: '40%',
};

EditLogModal.propTypes = {
  updateLog: PropTypes.func.isRequired,
  current: PropTypes.object,
};

const mapStateToProps = state => ({
  current: state.log.current,
});

export default connect(mapStateToProps, { updateLog })(EditLogModal);
